.home__wrapper{

    color: #664842 ;
    display: flex;
    flex-direction: column;
    margin: 0 0 0 30px;
    overflow: hidden;
}
.home__lable{
    color: #664842 ;
    font-size: 10vw;
    font-weight: 300;
    line-height: 0.7;

    margin: 50px 0 0 0;
}



.home__blockIMG{
    grid-area: blockIMG;
    display: block;
    width: 100%;
    margin: 0 0 0 150px;
    transition: all 1s ease;

}
.home__block1{
    grid-area: block1;
    width: 50%;

}
.block1__lable{
    font-weight: bold;
    font-size: 8vw;
    margin: 0 0 20px 0;
}
.home__leaveRequest{

}
.leaveRequest__text{

}

.leaveRequest__button{
    margin: 20px 0 0 0;
}

.home__block2_wrapper{
    grid-area: block2;

    text-align: center;
    margin: 7vw 3vw 0 0;
    display: grid;
    gap: 1vw;

    grid-template-columns: 1fr 1fr 1fr;
}


.home__block2_conteiner{
    display: flex;
    flex-direction: row;
    align-items: center;
}
.home__block2__icon{
    margin: 0 10px ;
}
.home__block2__text{
    font-size: 15px;
    font-weight: bold;
}
.home__block3_wrapper{
    width: 100%;
    text-align: center;
    margin: 14vw 0 0 0;
}
.home__block3_lable{
    font-size: 4vw;
}
.home__block3_icons{
    margin: 7vw 4vw 0 0;
    display: grid;
    gap: 1vw;
    grid-template-columns: 1fr 1fr 1fr 1fr;
}
.home__block3_conteiner{

}
.home__block3_icon{

}
@media screen and (max-width: 768px) {
    .home__animation_container{
        grid-template-columns: 1fr;

        grid-template-areas:
          "block1"
          "block2";
    }

    .home__blockIMG{
        display: none;
    }
    .home__block2_wrapper{
        grid-template-columns: 1fr 1fr;
    }
    .home__block2_conteiner{
        flex-direction: column;

    }
    .home__block2__text{
        font-size: 3vw;
        font-weight: bold;
    }
    .home__block3_icons{
        margin: 7vw 0 0 0;
        display: grid;
        gap: 1vw;
        grid-template-columns: 1fr 1fr;
    }
}
@media screen and (max-width: 1200px) and (min-width: 769px) {
    .home__animation_container{
        grid-template-columns: 1fr 1fr;;
        grid-template-areas:
          "block1 blockIMG"
          "block2 block2";
    }
    .home__blockIMG{

        margin: 0 0 0 50px;
    }
    .home__block1{

        width: 100%;

    }
    .home__block2_conteiner{
        flex-direction: column;

    }

}
@media screen and (min-width: 1201px) {
    .home__animation_container{
        grid-template-columns: 1fr 1fr;
        grid-template-areas:
          "block1 blockIMG"
          "block2 blockIMG";
    }
}