.wrapperApp {

    min-height: 100vh;
    position: relative;

    display: grid;
    grid-template-columns: auto 1fr;
    grid-auto-rows: auto 1fr auto;
    grid-template-areas:
          "menu content"
          "menu content"
          "menu footer";


    font-family: "Montserrat";
    font-size: 14px;
    line-height: 1.5;


}
.app__menuBottom{
    display: none;
    grid-area: menubottom;
    position: fixed;
    z-index: 200;
}
.app__menuBottom_icon img{
    margin: 10px 20px;
    transition: all 0.4s ease;

}
.app__menuBottom_icon:hover{
    border-radius: 100%;
    background-color: rgba(205, 214, 219, 0.3);
    transition: all 0.4s ease;
}
.app__menu {
    background-color: #80594d;
    display: block;
    grid-area: menu;

}
.app__menuMobile{
    background-color: #80594d;
    display: none;
    grid-area: menu;
}
.app__main {
    width: 100%;
    grid-area: content;
}

.app__footer {
    grid-area: footer;
}

.my_button{
    text-transform: uppercase;
    display: inline-block;
    background-color: #cc5d47;
    padding: 16px 30px;
    border-radius: 35px;
    color: white;
    font-size: 13px;
    transition: all 0.4s ease;
    cursor: pointer;
}
.my_button:hover{
    background-color: #d0b0aa;
    color: #664842;
    transition: all 0.4s ease;
}

@media screen and (max-width: 768px) {
    .app__menuMobile{
        display: block;
        grid-area: menu;
    }
    .app__menu {
        display: none;
        grid-area: menu;

    }
    .app__menuBottom{
        display: block;

    }
}
