.menuMobile__wrapper{

    display: flex;
    height: 100%;
    flex-direction: column;
    width: 80px;
    background-color: #80594d;
    position: relative;

}

.menuMobile__conteiner{
    padding: 60px 0 0 0 ;
    position: fixed;
    height:100%;
    overflow-Y: auto;
}
.menuMobile__conteiner::-webkit-scrollbar {
    width: 6px;
    background-color: #f7ece9;
}

.menuMobile__conteiner::-webkit-scrollbar-thumb {
    border-radius: 6px;
    background-color: #80594d;
}

.menuMobile__conteiner::-webkit-scrollbar-track {
    -webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.2);
    border-radius: 10px;
    background-color: #f7ece9;
}




.menuMobile_items{

}

.menuMobile_icon{
    padding: 15px 20px ;
    cursor: pointer;
    transition: all 0.4s ease;
}
.menuMobile_icon:hover{
    transition: all 0.4s ease;
    background-color: #8c685d;
}