.menu__wrapper{

    height: 80%;
    width: 270px;
    display: flex;
    flex-direction: column;
    padding: 20px 0 0 0 ;


    overflow: hidden;
    position: relative;

    color: white;
}

.myCursor{
    cursor: pointer;
}

.back_Logo{
    height: 500px;
    width: 750px;

    background-color: #f7ece9;
    border-radius: 50%;
    position: absolute;
    top: -300px;
    left: -280px;
    z-index: 50;

}
.menu__content{
    width: 80%;
    display: flex;
    flex-direction: column;
    margin: 20px;
    position: relative;
    z-index: 100;
}
.menu__content {
    text-decoration: none;
}
.menu__logo{
    color: black;
    width: 100%;
    height: 200px;


}
.logo__img{
    font-size: 30px;
    cursor: pointer;
}
.logo__text{
    margin: 20px 0 0 0;
}
.menu__contacts{

}
.menu__contacts_phone{
 display: flex;
    margin: 10px 0;
}
.phone_icon{
 margin: 0 8px;
}
.phone_tel{

}
.tel{
    display: block;
    height: 30px;
    font-size: 18px;
    cursor: pointer;
    transition: all 0.4s ease;
    margin: 4px 0;
}
.tel:hover{
    font-size: 19px;
    transition: all 0.4s ease;

}
.order_a_call{
    cursor: pointer;
}
.menu__operatingMode{

    display: flex;
    margin: 10px 0;
}
.operatingMode_icon_conteiner{
    display: flex;
    width: 100%;
    justify-content: space-between;
    cursor: pointer;
    margin: 20px 0;
}
.operatingMode_icon{
    margin: 0 8px ;
}
.operatingMode_text{

}
.menu__button{
    align-self:center;
    margin: 15px;

}

.menu__items a{
 color: white;
    text-decoration: none;
}
.menu__item {
    display: flex;
    align-items: center;
    padding: 10px;
    border-top: 1px #f7ece9 solid;
    cursor: pointer;
    transition: all 0.4s ease;

}
.menu__item:hover{
    background-color: #8c685d;
    transition: all 0.4s ease;

}
.menu__item_icon{
    margin: 0 6px 0 0;
}
.menu__item_text {


}